

.about_banner:before {
    content: '';
    display: block;
    clear: both;
    background: rgba(6, 6, 6, 0.41);
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.about_banner .caption {
    position: absolute;
    right: 15%;
    bottom: 5rem;
    left: 15%;
    z-index: 10;
    padding-top: 20px;
    padding-bottom: 20px;
    color: #fff;
    text-align: center;
    text-shadow: 0 1px 2px rgba(0, 0, 0, .6);
}

.about_banner .caption h1 {
    font-size: 32px;
    font-weight: bold;
    text-transform: uppercase;
}

.static_page {
    /*background: #ccc;*/
}

.static_page .container {
    padding-top: 130px;
}

.static_page h4 {
    font-size: 1rem;
    font-weight: bold;
}

.sucess-mess {
    display: block;
    text-align: center;
    min-height: 25rem;
}

.sucess-mess .check-success {
    width: 60px;
    height: 60px;
    border: 3px solid #ADA8A8;
    border-radius: 100%;
    -moz-border-radius: 100%;
    -ms-border-radius: 100%;
    -o-border-radius: 100%;
    -webkit-border-radius: 100%;
    padding: 5px;
    font-size: 32px;
    color: green;
    display: block;
    margin: 35px auto 5px;
}

.sucess-mess h3 {
    font-size: 28px;
    color: #444;
    margin: 7px;
}

.sucess-mess p {
    font-size: 12px;
    line-height: 25px;
    color: #504D4D;
}





.category-group {
    background: #EDF2F2;
    margin-bottom: 30px;
    padding: 40px 20px 35px;
    border-radius: 3px;
    min-height: 305px;
    text-align: center;
}
.category-group .icon {
    width: 50px;
    height: 50px;
    margin: 0 auto 15px;
}
.category-group img {
    max-width: 100%;
    height: auto;
    display: block;
}
.category-group h4 {
    line-height: 1.4;
    font-size: 18px;
    font-weight: 500;
}
.category-group p {
    font-size: 13px;
    line-height: 1.6;
}

.faq-thread {
    border-bottom: 1px solid #E5E9EA;
    padding-bottom: 30px;
    margin-bottom: 40px;
}




@media (max-width: 768px) {
    .about_banner {
        height: 280px;
    }

    .about_banner .caption {
        left: 0;
        right: 0;
    }

    .about_banner .caption h1 {
        font-size: 28px;
    }

    .card-block h3 {
        font-size: 1.5rem;
    }

    .static_page h1 {
        font-size: 1.5rem;
        font-weight: 500;
    }

    .static_page p {
        font-size: 14px;
        text-align: justify;
    }

    .pt-2 {
        padding: 0.7rem !important;
    }

    .p-5 {
        padding: 1.2rem !important;
    }

    .mb-2 {
        margin-bottom: 0.5rem !important;
    }

    .pt-2 {
        padding-top: 1rem !important;
    }
}

@media (min-width: 400px) and (max-width: 768px) {
    .polular_loc .p_loc_warp {
        width: 48%;
        margin-right: 3%;
        display: inline-block;
        float: left;
    }

    .polular_loc .p_loc_warp:last-child {
        margin-right: 0;
    }
}

@media (max-width: 400px) {
    .polular_loc .p_loc_warp:last-child {
        margin-bottom: 1rem;
    }
}


